import React, { useState } from 'react';
import {AppBar, Box, Button, Tabs,Tab, Toolbar, Typography } from '@mui/material';
import {useDispatch, useSelector} from 'react-redux';
import {Link} from 'react-router-dom';
import { authActions } from '../Store';
const Header = () => {
    const dispath = useDispatch();
    const isLoggedIn = useSelector((state) => state.isLoggedIn)
    const [value, setValue] = useState();
  return (
    <AppBar position='sticky' sx={{background:" linear-gradient(90deg, rgba(50,4,119,1) 0%, rgba(245,83,83,1) 94%, rgba(254,177,57,1) 100%)"}}>
        <Toolbar>
            <Typography variant="h4">
                BlogsApp
            </Typography>
            { isLoggedIn && <Box display="flex" marginLeft={'auto'} marginRight={'auto'}>
                <Tabs textColor='inherit' value={value} onChange={(e,val)=>setValue(val)}>
                    <Tab LinkComponent={Link} to="/blog" label="All Blogs"/>
                    <Tab LinkComponent={Link} to="/myblogs" label="My Blogs" />
                    <Tab LinkComponent={Link} to="/blogs/add" label="Add Blog" />
                </Tabs>
            </Box>}
             <Box display="flex" marginLeft="auto">
               { !isLoggedIn && <> <Button LinkComponent={Link} to="/auth" variant='contained' sx={{margin:1,borderRadius:10}} color='info'>Login</Button>
                </>}
                { isLoggedIn && <Button onClick={()=>dispath(authActions.logout())} LinkComponent={Link} to="/" variant='contained' sx={{margin:1,borderRadius:10}} color='info'>Logout</Button>}
            </Box>
        </Toolbar>
    </AppBar>
  )
}

export default Header