import { Button, InputLabel, TextField, Typography } from '@mui/material';
import { Box } from '@mui/system';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'


const labelStyle ={mb:1, mt:2, fontSize:'24px',fontweight:'bold'};
const BlogDetail = () => {
  const navigate =useNavigate();
  const [inputs, setInputs] = useState({
    
  })
  const handleChange =(e)=>{
    setInputs((prevState) =>({
      ...prevState,
      [e.target.name]: e.target.value
    }))
  }
  const [blog, setblog] = useState()
  const id = useParams().id;
  const fetchDetails =async ()=>{
    const res = await axios.get(`http://localhost:5000/api/blogs/${id}`)
      .catch(err =>console.log(err))
    const data = await res.data;
    return data;
  }
  useEffect(()=>{
    fetchDetails().then((data) => {
      setblog(data.blog)
      setInputs({title:data.blog.title,description:data.blog.description})});
  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[id]);
  const sendRequest = async() =>{
    const res =await axios.put(`http://localhost:5000/api/blogs/update/${id}`,{
      title:inputs.title,
      description: inputs.description
    }).catch(err =>console.log(err));
    const data = await res.data;
    return data;
  }
  console.log(blog);
  const handleSubmit =(e)=>{
    e.preventDefault()
    console.log(inputs);
    sendRequest().then((data)=>console.log(data)).then(()=>navigate("/myBlogs"))
  }
  return (
    <div>{ inputs && <form onSubmit={handleSubmit}>
    <Box 
      border={3} 
      borderColor=" linear-gradient(90deg, rgba(50,4,119,1) 0%, rgba(245,83,83,1) 94%, rgba(254,177,57,1) 100%)" 
      borderRadius={10} 
      boxShadow="10px 10px 20px #ccc" 
      padding={3}
      margin={'auto'}
      marginTop={3} 
      display='flex' 
      flexDirection={'column'} 
      width={'80%'}>
      <Typography fontWeight={'bold'} padding={3} color='grey' variant='h2' textAlign={'center'} >
        Edit Your Blog
      </Typography>
      <InputLabel sx={labelStyle}>Title</InputLabel>
      <TextField name='title' onChange={handleChange} value={inputs.title} margin='auto'  variant='outlined' />
      <InputLabel  sx={labelStyle} >Description</InputLabel>
      <TextField name='description' onChange={handleChange} value={inputs.description} margin='auto'  variant='outlined'/>
      <Button sx={{mt:2,borderRadius:4}}  color="warning" type='submit' variant='contained'> Submit</Button>
    </Box>
  </form>}</div>
  )
}

export default BlogDetail 